body{
	margin: 0;
	width: 100%;
	height: 100%;
	overflow: hidden;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	background: black;
}
a{
	text-decoration: none;
	
}

.nav-bar{
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: left;
	align-items: center;
	position: relative;
	
	background: whitesmoke;
	/* box-shadow: 0 5px 3px 0.1em whitesmoke; */
	/* overflow: auto; */

}
.nav-items{
	display: flex;
	justify-content: left;
	align-items: center;
}

.page-name{  /*SORTING ALGORITHMS*/
	display: block;
	padding: 1em;
	overflow: auto;

	color: black;
	font-size: 1.25rem;
	font-weight: bold;
	letter-spacing: 1px;
	/* float: left; */
}


/*#region Options Selector*/
.option-wrap{
	position: relative;
	display: block;
	min-width: 10ch;

}
.option-wrap select{
	display: none;
}
.select-items{
	position: absolute;
	color: whitesmoke;
	background-color: black;
	right: 0;
	left: 0;
	border: 0.1em solid whitesmoke;
	/* border-color: black whitesmoke whitesmoke whitesmoke; */
	z-index: 10;
}
.hide-items{
	display: none;
}
.current-select{
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	font-weight: bold;
	font-size: initial;
	
	padding: 1vh 2.75vw 1vh 1vw;
	min-width: 8vw;
	border: 0.25em solid black;
	border-radius: 5px;

	cursor: pointer;
	outline: none;
	transition: background 300ms ease-in, color 300ms ease-out;
}
.current-select:hover, .active{
	background: black;
	color: whitesmoke;
}
.current-select:focus-within{
	background: black;
	color: whitesmoke;
	box-shadow: 0 0 6px 1px gray;
}
.arrow-down{
	position: absolute;
	font-size: 1.5rem;
	right: 0.75vw;
	transform: translate(0,-15%);
}
.choices{
	font-weight: bold;
	text-align: center;
	padding: 1vh 0;
	transition: background 100ms ease-out, font-size 100ms ease-out;
	cursor: pointer;
}
.choices:hover{
	background: #6e6e6e; 
	/* background: whitesmoke; */
	/* color:black; */
	font-size: 1.2em;
}
/*#endregion */

/*#region Amount Input*/
#ip-name{ /*AMMOUNT NAME*/
	padding: 1vh 1vw;
	font-weight: bold;
	/* float: left; */
	margin: 0.5em 0px;
	/* padding-right: 4px; */
}
.ip{ /*AMOUNT*/
	width: 6vw;
	min-width: 6ch;

	padding: 1vh 1vw;
	margin: 0.5em;
	margin-left: 0.1em;
	
	border: 0.25em solid black;
	border-radius: 5px;

	background:whitesmoke;

	color: black;
	font-size: initial;
	font-weight: bold;

	outline: none;
	transition: background 300ms ease-out, color 300ms ease-out;
}
.ip:hover{
	background: black;
	color: whitesmoke;
}
.ip:focus{
	background: black;
	color: whitesmoke;
	box-shadow: 0 0 6px 1px gray;
}
/*#endregion */

/*#region Buttons*/
.btn{
	background: whitesmoke;
	border: none;
	flex-shrink: 1;

	font-size: initial;
	font-weight: bold;
	text-align: center;
	padding: 1vh 1vw;
	margin: 0.5em;
	display: block;
	cursor: pointer;
	
	transition: background 100ms ease-out , color 100ms ease-out, box-shadow 100ms ease-out ,transform 20ms ease-out;
}
.btn:hover{
	background: black;
	color: whitesmoke;
	box-shadow: 0 0 0 4px black;
	transition: background 100ms ease-in, color 100ms ease-in;
}
.btn:active{
	transform: scale(1.1);
	/* transition: transform 20ms ease-in; */
}

.btn:focus{
	outline: none;
	box-shadow: 0 0 0 4px black;
}
/*#endregion*/

/*#region Canvas */
.main-canvas{
	border: 2px solid black;
	background: black;
	/* width: 100%;
	height: 100%; */
}
